import React from 'react';
import { Document, Page } from 'react-pdf';
// import cvpdf from '../../public/pdfs/CV.pdf'

const cv = () => {
  return (
    <Document
      file={{
        url: "/pdfs/CV.pdf"
      }}
      onLoadError={e =>
        console.log("Error while loading document! " + e.message)
      }
      onSourceError={e =>
        console.log("Error while loading document! " + e.message)
      }
    >
      <Page pageNumber={1} />
    </Document>
  );
};

export default cv;
